import React, { useEffect, useState } from "react";
import tableau from "@styles/assets/images/hirescreens/media-icons/tableau.svg";
import unsplash from "@styles/assets/images/hirescreens/media-icons/unsplash.svg";
import powerpoint from "@styles/assets/images/hirescreens/media-icons/powerpoint.svg";
import power_bi from "@styles/assets/images/hirescreens/media-icons/power_bi.svg";
import googleslides from "@styles/assets/images/hirescreens/media-icons/googleslides.svg";

import figma from "@styles/assets/images/hirescreens/hire-product-designers/figma.svg";
import illustrator from "@styles/assets/images/hirescreens/hire-graphic-designers/adobe_illustrator.svg";
import Photoshop from "@styles/assets/images/hirescreens/hire-graphic-designers/adobe_photoshop.svg";
import notion from "@styles/assets/images/hirescreens/hire-product-designers/notion.svg";
import canva from "@styles/assets/images/hirescreens/hire-web-designers/canva.svg";

import Ticker from "@components/common/Ticker";

const datas = [
  { id: 0, title: "Illustrator", icon: illustrator },
  { id: 1, title: "Photoshop", icon: Photoshop },
  { id: 2, title: "Canva", icon: canva },
  { id: 3, title: "Microsoft PowerPoint", icon: powerpoint },
  { id: 4, title: "Figma", icon: figma },
  { id: 5, title: "Google Slides", icon: googleslides },
  { id: 6, title: "Unsplash", icon: unsplash },
  { id: 7, title: "Tableau", icon: tableau },
  { id: 8, title: "Notion", icon: notion },
  { id: 9, title: "Power BI", icon: power_bi },
];
const TechStackMarquee = () => {
  const [scrollDir, setScrollDir] = useState(null);

  useEffect(() => {
    let scrollSide = window.scrollY;

    const handleScroll = () => {
      let currentScrollSide = window.scrollY;

      if (currentScrollSide > scrollSide) {
        setScrollDir("down");
      } else if (currentScrollSide < scrollSide) {
        setScrollDir("up");
      }
      scrollSide = currentScrollSide;
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <section className="hire-marquee-wrapper">
      <div className="hire-developers-hero-wrapper item-center gap-lg">
        <div className="item-center gap-lg">
          <h2 className="about-h2">Our Technology Stack</h2>
          <p className="about-p c-text-center">
            Our Presentation Designers use a powerful creative stack to design
            visually compelling slides that enhance storytelling, brand
            communication, and audience engagement.
          </p>
        </div>
        <section className="token-dev-technology-marquee-section">
          <Ticker
            duration={30}
            direction={`${scrollDir === "up" ? 100 : -100}`}
          >
            {datas.map((item) => (
              <div
                className="token-dev-technology-card item-center"
                key={item.id}
              >
                <div className="flex-center">
                  <img src={item.icon} alt={item.title} />
                  <p className="about-p">{item.title}</p>
                </div>
              </div>
            ))}
          </Ticker>
          <Ticker
            duration={30}
            direction={`${scrollDir === "up" ? -100 : 100}`}
          >
            {datas.map((item) => (
              <div
                className="token-dev-technology-card item-center"
                key={item.id}
              >
                <div className="flex-center">
                  <img src={item.icon} alt={item.title} />
                  <p className="about-p">{item.title}</p>
                </div>
              </div>
            ))}
          </Ticker>
        </section>
      </div>
    </section>
  );
};

export default TechStackMarquee;
